.service{
  &__flex{
    display: flex;
    justify-content: center;
    margin-top: 80px;
    @include m1600{
      margin-top: vw(80);
    }
    @include m1260{
      margin-top: vw2(70);
    }
    @include m768{
      margin-top: vw3(54);
      display: block;
      padding: 0 vw3(20);
      box-sizing: border-box;
    }
  }
  &__item{
    padding: 64px 54px 0;
    display: flex;
    flex-direction: column;
    border-left: 1px solid $mainColor;
    transition: 0.4s;
    position: relative;
    @include m1600{
      padding: vw(64) vw(54) 0;
    }
    @include m1260{
      padding: vw2(65) vw2(51) 0;
    }
    @include m768{
      border-left: none;
      border-top: 1px solid $mainColor;
      padding: vw3(21) 0;
    }
    &:last-child{
      border-right: 1px solid $mainColor;
      @include m768{
        border-right: none;
        border-bottom: 1px solid $mainColor;
      }
    }
    &-title{
      font-weight: 600;
      font-size: 72px;
      line-height: 85px;
      transition: 0.4s;
      @include m1600{
        font-size: vw(72);
        line-height: vw(85);
      }
      @include m1260{
        font-size: vw2(36);
        line-height: vw2(42);
      }
      @include m768{
        font-size: vw3(24);
        line-height: vw3(28);
        text-align: center;
      }
      &::after{
        content: "";
        display: block;
        width: 198px;
        height: 10px;
        background: $mainColor;
        margin-top: 12px;
        transition: 0.4s;
        @include m1600{
          height: vw(10);
        }
        @include m1260{
          width: vw2(200);
          height: vw2(4);
          margin-top: vw2(15);
        }
        @include m768{
          width: vw3(100);
          height: vw3(4);
          margin-top: vw3(15);
        }
      }
    }
    &-list{
      font-size: 16px;
      line-height: 31px;
      flex-grow: 1;
      margin-top: 30px;
      transition: 0.4s;
      @include m1600{
        margin-top: vw(30);
      }
      @include m1260{
        margin-top: vw2(15);
      }
      @include m768{
        margin-top: vw3(15);
      }
      li{
        padding-left: 30px;
        position: relative;
        @include m1600{
          padding-left: vw(30);
        }
        @include m1260{
          padding-left: vw2(20);
        }
        @include m768{
          padding-left: vw3(20);
        }
        +li{
          margin-top: 20px;
          @include m1600{
            margin-top: vw(15);
          }
          @include m1260{
            margin-top: vw2(8);
          }
          @include m768{
            margin-top: vw3(3);
          }
        }
        &::before{
          content: "";
          display: block;
          height: 1px;
          width: 20px;
          background: $mainColor;
          position: absolute;
          left: 0;
          top: 50%;
          @include m1600{
            width: vw(20);
          }
          @include m1260{
            width: vw2(10);
          }
          @include m768{
            width: vw3(10);
          }
        }
      }
    }
    &-link{
      transition: 0.4s;
      margin-top: 30px;
      @include m1600{
        margin-top: vw(30);
      }
      @include m1260{
        margin-top: vw2(30);
      }
      @include m768{
        margin-top: vw3(45);
      }
    }


    &:not(.active){
      cursor: pointer;
      padding: 60px 70px;
      @include m1600{
        padding: vw(60) vw(70);
      }
      @include m1260{
        padding: vw2(80) vw2(27);
      }
      @include m768{
        padding: vw3(15) 0;
      }
      .service__item-title{
        font-weight: normal;
        writing-mode: vertical-lr;
        text-orientation: mixed;
        text-align: center;
        flex-grow: 1;
        transform: rotate(-180deg);
        @include m768{
          transform: none;
          writing-mode: horizontal-tb;
        }
        &::after{
          margin-top: 0;
          height: 0;
          width: 0;
        }
      }
      .service__item-list{
        display: none;
      }
      .arrow-link{
        display: none;
      }
      &:hover{
        padding: 60px 80px;
        @include m1600{
          padding: vw(60) vw(80);
        }
        @include m1260{
          padding: vw2(80) vw2(27);
        }
        @include m768{
          padding: vw3(15) 0;
        }
      }
    }
  }
}